* {
  box-sizing: border-box;
}

body {
  background-image: url('https://cdn.pixabay.com/photo/2021/10/16/13/55/abstract-6715107_1280.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0 20px;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}
li {
  list-style: none;
}
input,
button,
a,
label {
  font-size: 30px;
  font-family: 'Permanent Marker', sans-serif;
  color: #212121;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}
.timer {
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  width: 700px;

  display: flex;
  align-items: center;
  justify-content: space-around;

  font-family: 'Permanent Marker', sans-serif;
  font-size: 30px;
  color: #212121;
}
.form{
  display:inline-flex;
  flex-direction: column;

}
.form button{
  width: 300px;
}